.left{
    border: 2px solid rgb(221, 221, 221);
    display: inline-block;
    box-shadow: 1px 3px 10px grey;
    padding: 10px 20px 0 0;
    height: 97%;
    margin-top: 20px;
    border-end-end-radius: 30px;
    border-top-right-radius: 30px;
    &__div{
        list-style-type: none;
        padding: 0;
        display: inline-block;
        margin: 0;
        border: 2px solid rgb(221, 221, 221);
        padding: 40px 20px;
        border-end-end-radius: 30px;
        border-top-right-radius: 30px;
    }
    &__link{
        text-decoration: none;
        color: #1959D6;
        font-size: 28px;

        border: 2px solid rgb(221, 221, 221);
        border-radius: 10px;
        padding: 10px 25px;
        margin-bottom: 10px;
        font-weight: bold;
        display: block;
        &--active{
            background: #1959D6;
            box-shadow: 0px 2px 10px 1px rgba(0, 0, 0, 0.15);
            border-radius: 10px;
            color: white;
        }
    }
    &__heading{
        color: #1959D6;
        font-size: 28px;
        text-align: center;
    }
}