@import url('https://fonts.googleapis.com/css2?family=Montserrat&display=swap');

body{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: 'Montserrat', sans-serif;
}

.container{
  width: 1300px;
  margin: 0 auto;
}
.container2{
  width: 1340px;
  margin: 0 auto;
}


.app{
  &__div{
    width: 1340px;
    display: flex;
    justify-content: space-between;
  }
  &__nav{
    width: 75vw;
    margin: auto;
    height: 100vh;
    background: #FFFFFF;
    box-shadow: 0px 4px 10px 8px rgba(0, 0, 0, 0.15);
    border-radius: 30px;
    margin-top: 25px;
    padding: 100px 0;
  }
}