.header{
    border: 2px solid rgb(221, 221, 221);
    border-end-end-radius: 20px;
    border-end-start-radius: 20px;
    box-shadow: 10px 2px 10px rgb(221, 221, 221);
    &__list{
        list-style-type: none;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 0;
    }
    &__icon{
        border: 2px solid rgb(168, 168, 168);
        padding: 12px;
        border-radius: 10px;
        width: 15px;
    }
    &__span{
        color: rgb(7, 248, 7);
        font-size: 20px;
        position: absolute;
        top: 35px;
        margin-left: 10px;
    }
    &__input{
        border: 2px solid rgb(195, 195, 195);
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 0 20px;
        border-radius: 20px;
        &2{
            border: none;
            padding: 10px 20px;
            outline: none;
        }
    }
    &__image{
        width: 300px;
    }
    &__img{
        width: 80px;
    }
}