.language{
    &__list{
        list-style-type: none;
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 60vw;
        margin: auto;
    }
    &__heading{
        font-size: 40px;
        font-weight: bold;
        color: #1959D6;
        text-align: center;
        margin: 60px 0;
    }
    &__img{
        width: 180px;
        border-radius: 30px;
        border: 1px solid rgb(212, 212, 212);
    }
}