.dostup{
    padding: 0 100px;
    &__heading{
        font-size: 40px;
        color: #1959D6;
        font-weight: bold;
        text-align: center;
    }
    &__text{
        font-size: 25px;
        font-weight: 500;
        color: #000;
    }
    &__select{
        padding: 10px 20px;
        width: 270px;
        outline: none;
        background: #FFFFFF;
        border: 1px solid #E2E2E2;
        box-sizing: border-box;
        box-shadow: inset 0px 2px 4px 2px rgba(0, 0, 0, 0.25);
        border-radius: 30px;
    }
    &__title{
        font-size: 30px;
        font-weight: bold;
        color: #000;
    }
    &__input{
        background: #FFFFFF;
        box-shadow: inset 0px 0px 15px 2px rgba(67, 67, 67, 0.15);
        border-radius: 20px;
        padding: 10px 30px;
        outline: none;
        border: none;
        width: 350px;
    }
    &__form{
        width: 630px;
        display: flex;
        justify-content: space-between;
    }
    &__menu{
        width: 600px;
        margin: 0 auto;
        list-style-type: none;
        display: flex;
     //    padding: 0;
        justify-content: space-between;
        padding-top: 150px;
    }
    &__btn--red{
        width: 200px;
        padding: 10px 30px;
        color: #FFFFFF;
        font-weight: bold;
        font-size: 16px;
        background: #CC2424;
        box-shadow: 0px 2px 10px 1px rgba(0, 0, 0, 0.15);
        border-radius: 20px;
        border: none;
        cursor: pointer;
        border: 2px solid #CC2424;
        &:hover{
            color: #CC2424;
            background-color: transparent;
        }
    }
    &__btn--green{
        width: 200px;
        padding: 10px 30px;
        color: #FFFFFF;
        font-weight: bold;
        font-size: 16px;
        background: #3CCC24;
        box-shadow: 0px 2px 10px 1px rgba(0, 0, 0, 0.15);
        border-radius: 20px;
        border: none;
        cursor: pointer;
        border: 2px solid #3CCC24;
        &:hover{
            background-color: transparent;
            color: #3CCC24;
        }
     }
}