.footer{
    border: 2px solid rgb(221, 221, 221);
    border-top-left-radius: 30px;
    border-top-right-radius: 30px;
    margin-top: 30px;
    &__list{
        list-style-type: none;
        display: flex;
        justify-content: space-between;
        padding: 30px;
        text-transform:uppercase;
    }
    &__item{
        display: flex;
    }
    &__text{
        color: #1959D6;
        font-weight: 500;
    }
    &__submenu{
        width: 100px;
        list-style-type: none;
        display: flex;
        padding: 0;
        justify-content: space-between;
    }
    &__heading{
        color: #1959D6;
        font-size: 21px;
    }
    &__link{
        display: block;
        text-decoration: none;
        color: #1959D6;
        font-size: 18px;
        font-weight: 500;
        margin: 40px 0;
    }
    &__sublink{
        text-decoration: none;
        color: #1959D6;
    }
    &__hr{
        color: #1959D6;
        border: 0.1px solid #1959D6;
        margin-top: 30px;
        height: 200px;
        margin-left: 20px;
    }
}