.data{
   width: 75vw; 
   &__heading{
       font-size: 40px;
       color: #1959D6;
       text-align: center;
       font-weight: bold;
   }
   &__list{
       list-style-type: none;
       width: 400px;
       margin: 0 auto;
       padding: 0;
   }
   &__item{
       margin: 30px 0;
   }
   &__input{
       padding: 20px;
       width: 100%;
       border: none;
       outline: none;
       background: #FFFFFF;
       box-shadow: inset 0px 0px 15px 2px rgba(67, 67, 67, 0.15);
       border-radius: 20px;
   }
   &__menu{
       width: 600px;
       margin: 0 auto;
       list-style-type: none;
       display: flex;
       justify-content: space-between;
   }
   &__btn--red{
       width: 200px;
       padding: 10px 30px;
       color: #FFFFFF;
       font-weight: bold;
       font-size: 16px;
       background: #CC2424;
       box-shadow: 0px 2px 10px 1px rgba(0, 0, 0, 0.15);
       border-radius: 20px;
       border: none;
       cursor: pointer;
       border: 2px solid #CC2424;
       &:hover{
           color: #CC2424;
           background-color: transparent;
       }
   }
   &__btn--green{
       width: 200px;
       padding: 10px 30px;
       color: #FFFFFF;
       font-weight: bold;
       font-size: 16px;
       background: #3CCC24;
       box-shadow: 0px 2px 10px 1px rgba(0, 0, 0, 0.15);
       border-radius: 20px;
       border: none;
       cursor: pointer;
       border: 2px solid #3CCC24;
       &:hover{
           background-color: transparent;
           color: #3CCC24;
       }
    }
    &__select{
        width: 440px;
        color: #999;
        cursor: pointer;
    }
}